/* loader items here */
.spinner {
  margin: 61px auto;
  width: 50px;
  height: 94px;
  text-align: center;
  font-size: 10px;
}

.spinner > div {
  background-color: #75B0FF;
  height: 100%;
  width: 6px;
  display: inline-block;

  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}

.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner .rect3 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.spinner .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.spinner .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

@-webkit-keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}

@keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}


/* animation items here */
@-webkit-keyframes fadeShift { from { opacity:0; -webkit-transform: translateX(-4px); transform: translateX(-4px) } to { opacity:1; -webkit-transform: translateX(0px); transform: translateX(0px) } }
@keyframes fadeShift { from { opacity:0; -webkit-transform: translateX(-4px); transform: translateX(-4px) } to { opacity:1; -webkit-transform: translateX(0px); transform: translateX(0px) } }
@-webkit-keyframes fadeOnly { from { opacity:0 } to { opacity:1 } }
@keyframes fadeOnly { from { opacity:0 } to { opacity:1 } }
@-webkit-keyframes popOut { from { opacity:0; -webkit-transform: scale(0.9); transform: scale(0.9) } to { opacity:1; -webkit-transform: scale(1); transform: scale(1) } }
@keyframes popOut { from { opacity:0; -webkit-transform: scale(0.9); transform: scale(0.9) } to { opacity:1; -webkit-transform: scale(1); transform: scale(1) } }
@-webkit-keyframes fadeDown { from { opacity:0; -webkit-transform: translateY(-8px); transform: translateY(-8px) } to { opacity:1; -webkit-transform: translateY(0px); transform: translateY(0px) } }
@keyframes fadeDown { from { opacity:0; -webkit-transform: translateY(-8px); transform: translateY(-8px) } to { opacity:1; -webkit-transform: translateY(0px); transform: translateY(0px) } }

